import { useGetDashboardMetricsQuery } from '@/state/api';
import numeral from 'numeral';
import React from 'react';
import GhanaCediIcon from '../{components}/Icons/ghCediIcon';


const CardPurchaseSummary = () => {
  const { data: dashboardMetrics, isError, isLoading } = useGetDashboardMetricsQuery();
  const purchaseData = dashboardMetrics?.purchaseSummary || [];

  const lastDataPoint = purchaseData[purchaseData.length - 1] || null;

  return (
    <div className="row-span-2 xl:row-span-3 col-span-1 md:col-span-2 xl:col-span-1 bg-white">
    
      {isLoading ? 
        <div className="m-5">Loading...</div>
       : 
        <>
          {/* header */}
          <div>
            <h2 className="text-lg font-semibold mb-2 px-7 pt-5">Purchase Summary</h2>
            <hr />
          </div>

          {/* body */}
                <div className="mb-4 mt-7 px-7"> 
        <p className="text-xs text-gray-400">Purchased</p>
        <div className="flex items-center">
          <p className="text-2xl font-bold">
            {lastDataPoint ? (
              <>
                <GhanaCediIcon className="w-6 h-6 inline mr-1" />
                {numeral(lastDataPoint.totalValue).format('0.00a')}
              </>
            ) : (
              'N/A'
            )}
          </p>
        </div>
      </div>
        </>
      } 
        </div>
        )
      }

export default CardPurchaseSummary;